.release {
  padding: 20px 0px;

  .release__column {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    @include media-query($tablet) {
      max-width: 999px;
      margin: 0 auto;
    }

    .release__card {
      display: flex;
      min-width: 185px;
      max-width: 250px;
      flex-grow: 1;
      flex-direction: column;
      background-color: var(--color-white);
      cursor: pointer;
      border-radius: 15px 15px 5px 5px;
      margin: 0 20px 20px 0;

      @include media-query($tablet) {
        min-width: 220px;
      }

      &:last-child {
        margin-right: 0;
      }

      img {
        width: 100%;
        filter: brightness(0.6);
        border-radius: 15px 15px 0px 0px;
      }

      .card-text {
        z-index: 1;

        h2 {
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 16px;
          height: 50px;
        }
      }

      &:hover {
        filter: drop-shadow(2px 3px 5px var(--color-black));

        img {
          filter: none;
        }

        h2 {
          color: var(--color-green);
        }
      }
    }
  }

  .release__button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    width: 100%;
    max-width: 200px;
    background-color: var(--color-green);
    color: var(--color-white);
    font: 700 16px $font_noto;
    line-height: 35px;
    -webkit-border-radius: 15px;
    border-radius: 15px;
    margin-bottom: 20px;

    &:hover {
      background-color: var(--color-green-hover);
    }
  }
}







